.nav-bar{
  background-color: #7ff087;
  height: 9vh;
  display: flex;
  justify-content: space-between;
}
.nav-bar > svg {
    width: 2vmax;
    height: 2vmax;
    margin: 1vmax 1vmax;
  }
  .nav-bar >svg{
    font-size: 2.5vmax;
    transition: all 0.5s;
    color: rgba(0, 0, 0, 0.445);
  }
  .nav-bar > svg:hover{
    color: black;
    transform: scale(1.2);
    cursor: pointer;
  }
  .nav-bar > div{
    height: inherit;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .nav-bar > div > svg{
    font-size: 2.5vmax;
    transition: all 0.5s;
    color: rgba(0, 0, 0, 0.445);
    margin-top: 0.8vmax;
    margin-right: 2vmax;
  }
  .nav-bar > div > svg:hover{
    color: black;
    transform: scale(1.2);
    cursor: pointer;
  }
  .hide{
    display: none;
  }
  .show{
    display: flex;
    flex-direction: column;
    background-color: #ebfaef;
    margin-right: 1vmax;
    border-radius: 8px;
    z-index: 1;
  }
  .show > div{
    display: flex;
    flex-direction: row;
    margin: 1vmax 1vmax;
    transition: all 0.5s;
  }
  .show > div:hover{
    color: black;
    transform: scale(1.2);
    cursor: pointer;
  }
  .show > div > svg{
    font-size: 1.5vmax;
    margin-right: 0.5vmax;
    width: 1.6vmax;
    height: 1.8vmax;
  }
  @media screen and (max-width:960px){
    .nav-bar > svg {
        width: 6vw;
        height: 5vw;
        margin: 1vw 2vw;
    }
    .nav-bar > div > svg{
        width: 6vw;
        height: 5vw;
        margin: 1vw 2vw;
    }
    .show > div > svg{
      width: 6vw;
      height: 5vw;
      margin: 1vw 2vw;
  }
  }
  @media screen and (max-width:600px){
    .nav-bar > svg {
        width: 8vw;
        height: 8vw;
        margin: 2vw 3vw;
    }
    .nav-bar > div > svg{
        width: 8vw;
        height: 8vw;
        margin: 2vw 3vw;
    }
    .show > div > svg{
      width: 7vw;
      height: 7vw;
      margin: 1vw 0.5vw;
  }
  }